body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:'Montserrat', sans-serif;box-sizing:border-box}.color-primary{color:#5f85db}.tab-manager{display:flex;padding:20px;justify-content:center;background:#eee}.tab-manager .tab{cursor:pointer;padding:12px 36px;margin:0 12px;font-size:12px;transition:opacity 0.3s ease-in-out;color:#353941;border-bottom:2px solid transparent}.tab-manager .tab:hover{border-bottom:2px solid #5f85db}.tab-manager .tab.selected-tab{color:#353941;font-weight:700;border-bottom:2px solid #5f85db}.tab-content{background:#eee;min-height:87.1vh;display:flex;color:#353941}.intro{min-height:100vh;display:flex;background:#eee;color:#353941;align-content:center}.intro .intro-background{height:100%;position:absolute}.intro .intro-background img{height:100%;margin:auto;z-index:1;opacity:.4}.intro .intro-content{display:block;margin:auto;text-align:right;z-index:2;color:#353941}.intro .intro-content .content-name{padding:0;margin:0;font-size:9vw;letter-spacing:4px;text-transform:uppercase;font-weight:700}.intro .intro-content .content-button{margin-top:10px;padding:6px 20px;border:1px solid #353941;border-radius:4px;background-color:transparent;text-decoration:none}.intro .intro-content .content-button span{font-weight:1.7rem;color:#353941;font-size:14px}.intro .intro-content .content-button span .fa-chevron-right{margin-left:12px}.intro .intro-content .content-button:hover,.intro .intro-content .content-button:focus{background:#353941;transition:all .3s ease-out;cursor:pointer}.intro .intro-content .content-button:hover span,.intro .intro-content .content-button:focus span{color:#eee;transition:all .2s ease-out}@media screen and (max-width: 768px){.intro{background-color:#353941}.intro .intro-background{position:absolute;display:flex;background:#353941}.intro .intro-background img{max-width:100vw !important;height:auto;position:absolute;bottom:0}.intro .intro-content{padding:0px 30px;color:#eee}.intro .intro-content .content-name{text-align:center;font-size:4vw;margin-bottom:10px}.intro .intro-content .content-button{margin:auto;display:flex;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:.7vw 3vw;border-color:#eee}.intro .intro-content .content-button .fa-chevron-right{color:#eee;font-size:2vw}.intro .intro-content .content-button span{color:#eee;font-size:2vw}}.about{display:block;padding:2vh 10vw;width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;text-align:justify;margin:auto}.about .about-content{display:block;width:100%}.about .about-content .about-description{display:block;margin:auto;width:60%;text-align:center}.about .about-content .about-description p{padding:0;margin:0;font-size:3rem;color:#4a4e54}.contact{margin:auto;width:400px;height:500px;position:relative}.contact .contact-card{position:absolute;top:50%;left:50%;width:350px;height:450px;transform:translate(-50%, -50%);border-radius:16px;overflow:hidden;box-shadow:0 5px 18px rgba(53,57,65,0.2);cursor:pointer;transition:0.5s}.contact .contact-card .contact-image{position:absolute;width:100%;height:100%;z-index:2;background-color:#eee;transition:.5s}.contact .contact-card .contact-image img{height:350px;position:absolute;bottom:0}.contact .contact-card:hover img{opacity:0.4;transition:.5s}.contact .contact-card:hover .contact-image{transform:translateY(-100px);transition:all .9s}.contact .social-icons{position:absolute;top:50%;left:45%;transform:translate(-50%, -50%);z-index:3;display:flex}.contact .social-icons li{list-style:none}.contact .social-icons li a{position:relative;display:block;width:50px;height:50px;line-height:50px;text-align:center;background:#eee;font-size:23px;color:#353941;font-weight:bold;margin:0 6px;transition:.4s;transform:translateY(200px);opacity:0}.contact .contact-card:hover .social-icons li a{transform:translateY(0px);opacity:1}.contact .social-icons li a:hover{background:#26282b;transition:.2s;color:#eee}.contact .contact-card:hover li:nth-child(1) a{transition-delay:0.1s}.contact .contact-card:hover li:nth-child(2) a{transition-delay:0.2s}.contact .contact-card:hover li:nth-child(3) a{transition-delay:0.3s}.contact .contact-card:hover li:nth-child(4) a{transition-delay:0.4s}.contact .contact-card:hover li:nth-child(5) a{transition-delay:0.5s}.contact .contact-details{position:absolute;bottom:0;left:0;background:#26282b;color:#eee;width:100%;height:120px;z-index:1;padding:10px}.contact .contact-details h2{margin:30px 0;padding:0;text-align:center}.contact .contact-details h2 .job-title{font-size:1rem;line-height:2.5rem;color:#eee;font-weight:300}

